import React, { useState } from "react";
import Header from "./Header";
import Note from "./Note";
import CreateArea from "./CreateArea";
import Footer from "./Footer";



function App() {

  const [notes, setNotes] = useState([])

  function addNote(note) {
    setNotes(prevValue => {
      return [...prevValue, note];
    });

    console.log(notes);
  }

  function deleteNote(id) {
    setNotes((prevValue) => {
      return (
        prevValue.filter((notes, index) => {
          return index !== id;
        })
      );
    })
  }

  return (
    <div>
      <Header />
      <CreateArea
        note={addNote}
      />

      {notes.map((data, index) => {
        return (
          <Note key={index} id={index} title={data.title} content={data.content} delete={deleteNote} />
        )

      })}

      <Footer />

    </div>
  );
}

export default App;
